.stage {
    width: fit-content;
    height: 50%;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin: 0  auto;
  }
  .thuy-wrapper {
    position: relative;
    color: rgb(0, 0, 0);
    font-size: 50px;
    text-transform: uppercase;
    letter-spacing: 0.25rem;
    padding-top: 0.65rem;
    padding-left: 0.5rem;
    padding-right: 0.36rem;
    padding-bottom: 0.2rem;
    font-weight: bold;
  }
  @media (max-width: 978px) {
    .thuy-wrapper{
        font-size: 30px;
    }
  }
  
  .sides {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
  }
  .sides .side {
    position: absolute;
    background: rgb(0, 0, 0);
    border: 10px;
  }
  .sides .side:nth-child(1) {
    top: 0;
    left: 0;
    width: 100%;
    height: 0.15rem;
    transform: translateX(-101%);
    animation: side-top ease 6s infinite;
  }
  .sides .side:nth-child(2) {
    top: 0;
    right: 0;
    width: 0.15rem;
    height: 100%;
    transform: translateY(-101%);
    animation: side-right ease 6s infinite;
  }
  .sides .side:nth-child(3) {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.15rem;
    transform: translateX(101%);
    animation: side-bottom ease 6s infinite;
  }
  .sides .side:nth-child(4) {
    top: 0;
    left: 0;
    width: 0.15rem;
    height: 100%;
    transform: translateY(101%);
    animation: side-left ease 6s infinite;
  }
  .text {
    position: relative;
  }
  .text--backing {
    opacity: 0;
  }
  .text--left {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .text--left .inner {
    transform: translateX(100%);
    animation: text-left 6s ease-in-out infinite;
  }
  .text--right {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .text--right .inner {
    transform: translateX(-200%);
    animation: text-right 6s ease-in-out infinite;
  }
  @-moz-keyframes slash {
    0% {
      transform: translate(-50%, -50%) rotate(-24deg) scaleY(0);
    }
    6% {
      transform: translate(-50%, -50%) rotate(-24deg) scaleY(1);
    }
    13% {
      transform: translate(-50%, -50%) rotate(-24deg) scaleY(1);
    }
    16.6% {
      transform: translate(-50%, -50%) rotate(-24deg) scaleY(0);
    }
    100% {
      transform: translate(-50%, -50%) rotate(-24deg) scaleY(0);
    }
  }
